@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{display:flex;align-items:center;flex-direction:column}body{background:#281f35;background:linear-gradient(to right, #281f35, #3d2e53);color:#ebebeb;font-family:"Architects Daughter", cursive}h1{margin:1em 0 0 0;font-size:3rem}h2{margin-bottom:2em}.radio{max-width:60em;width:100%}.filters{display:flex;justify-content:center;flex-wrap:wrap;margin-bottom:2em;font-size:1.2rem;width:100%}.filters span{margin:0.25em;border:1px solid #e36bae;border-radius:10px;padding:0.25em 0.75em}.filters span:hover{cursor:pointer;background:linear-gradient(to bottom right, #e36bae, #fb743e)}.filters .selected{background:linear-gradient(to bottom right, #e36bae, #fb743e)}.stations{display:grid;grid-template-columns:repeat(3, 33%);width:100%}.station{font-size:1.2em;border:1px solid #4c3e5f;margin:0.25em;border-radius:10px;padding:1em;display:flex;flex-direction:column;align-items:center;justify-content:center}.station:hover{border-color:#e36bae}.stationName{display:flex;flex-direction:row;margin-bottom:1em;width:100%;align-items:center;justify-content:start}.name{width:100%;text-align:center}img{display:block;width:2.5em;height:2.5em;border-radius:50%;border:2px solid #4c3e5f;margin:0 0.25em}audio{width:100%}.rhap_container.player{background-color:#4c3e5f;display:flex;justify-items:center;padding:0.25em 0.75em;border-radius:10px}.rhap_stacked .rhap_controls-section{margin-top:0 !important}.rhap_controls-section .rhap_volume-controls{justify-content:center}.rhap_controls-section .rhap_main-controls button[aria-label="Play"] svg path{fill:#ebebeb !important}.rhap_controls-section .rhap_main-controls button[aria-label="Pause"] svg path{fill:#e36bae !important}@media only screen and (max-width: 600px){.stations{grid-template-columns:repeat(2, 50%)}img{display:none}}

.lds-roller {
  display: flex;
  position: relative;
  width: 100%;
  height: 100vh;
  align-items: center;
  justify-content: center;
}
.lds-roller div {
  -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  -webkit-transform-origin: 40px 40px;
          transform-origin: 40px 40px;
}
.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #fff;
  margin: -4px 0 0 -4px;
}
.lds-roller div:nth-child(1) {
  -webkit-animation-delay: -0.036s;
          animation-delay: -0.036s;
}
.lds-roller div:nth-child(1):after {
  top: 63px;
  left: 63px;
}
.lds-roller div:nth-child(2) {
  -webkit-animation-delay: -0.072s;
          animation-delay: -0.072s;
}
.lds-roller div:nth-child(2):after {
  top: 68px;
  left: 56px;
}
.lds-roller div:nth-child(3) {
  -webkit-animation-delay: -0.108s;
          animation-delay: -0.108s;
}
.lds-roller div:nth-child(3):after {
  top: 71px;
  left: 48px;
}
.lds-roller div:nth-child(4) {
  -webkit-animation-delay: -0.144s;
          animation-delay: -0.144s;
}
.lds-roller div:nth-child(4):after {
  top: 72px;
  left: 40px;
}
.lds-roller div:nth-child(5) {
  -webkit-animation-delay: -0.18s;
          animation-delay: -0.18s;
}
.lds-roller div:nth-child(5):after {
  top: 71px;
  left: 32px;
}
.lds-roller div:nth-child(6) {
  -webkit-animation-delay: -0.216s;
          animation-delay: -0.216s;
}
.lds-roller div:nth-child(6):after {
  top: 68px;
  left: 24px;
}
.lds-roller div:nth-child(7) {
  -webkit-animation-delay: -0.252s;
          animation-delay: -0.252s;
}
.lds-roller div:nth-child(7):after {
  top: 63px;
  left: 17px;
}
.lds-roller div:nth-child(8) {
  -webkit-animation-delay: -0.288s;
          animation-delay: -0.288s;
}
.lds-roller div:nth-child(8):after {
  top: 56px;
  left: 12px;
}
@-webkit-keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

